<template>
	<div class="wrapper">
		<BaseButton
			v-show="showButton"
			:color="'white'"
			:active="activeDashboardCategory === 'create'"
			@click="switchDashboardCategory('create')"
		>
			Создать
		</BaseButton>
		<BaseButton
			:color="'white'"
			:active="activeDashboardCategory === 'list'"
			@click="switchDashboardCategory('list')"
		>
			Список
		</BaseButton>
	</div>
</template>

<script>
import BaseButton from '@/components/BaseButton.vue';
import { mapGetters } from 'vuex';
export default {
	components: {
		BaseButton,
	},
	data() {
		return {
			activeButton: 'create',
		};
	},
	computed: {
		...mapGetters(['getActiveDashboard', 'getActiveDashboardCategory']),
		activeDashboard() {
			return this.getActiveDashboard;
		},
		activeDashboardCategory() {
			return this.getActiveDashboardCategory;
		},
		showButton() {
			return this.getActiveDashboard !== 'reports'; 
		},
	},
	methods: {
		switchDashboardCategory(category) {
			this.$router.push({ name: this.activeDashboard });
			this.$store.commit('setActiveDashboardCategory', category);
			this.$store.commit('setActiveNestedDashboard', '');
		}
	},
};
</script>

<style lang="scss" scoped>
	.wrapper {
		display: flex;
		justify-content: space-between;
	}
</style>